/* Navbar */

body:not(.has_hero) .collapse.navbar-collapse {
    background-color: var(--navbar-bgc);
 
    transition: all .5s;
}

body.has_hero #navbarCollapse.collapse.navbar-collapse.bg-dark {
    background-color: transparent !important;
    transition: all 500ms;

}

body.has_hero #navbarCollapse.collapse.navbar-collapse.bg-dark.show {
    background-color: var(--navbar-bgc) !important;
}

.navbar-collapse.collapse.show,
.navbar-collapse.collapsing {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh !important;
    padding: 30px;
    z-index: 1200;
    overflow-y: auto;
    transition: all .5s;
    background-color: var(--navbar-bgc) !important;
    padding: 100px 0 0 0;


}

 

.navbar-collapse.collapsing {
    position: absolute;

    left: -500px;

    background-color: var(--navbar-bgc) !important;

}




body.navbar-fixed nav.navbar  {

    position: fixed!important;
    top: 0;
    /* Position the navbar at the top of the page */
    width: 100%;
    /* Full width */
    z-index: 1000;

}


body.navbar-auto nav.navbar {

    position: fixed!important;
    top: 0;
    /* Position the navbar at the top of the page */
    width: 100%;
    /* Full width */
    z-index: 1000;
    transform: translateY(0);
    transition: transform 0.3s ease-in-out;
    
}

 
body.navbar-auto nav.navbar.hidden-navbar {
    transform: translateY(-120%);
 
}

body.navbar-fixed main, body.navbar-auto main {

    padding-top: 100px!important;
    
}

body.has_hero nav.navbar {

    position:absolute;
    top: 0;
    /* Position the navbar at the top of the page */
    width: 100%;
    /* Full width */
    z-index: 1000;
}


 



body.navbar-fixed:not(.has_hero) {

    padding-top: 100px !important;
}

body.has_hero {

    padding-top: 0 !important;
}


.mainlinks {

    padding: 0;
    height: 60px;


}


nav.navbar {
    height: 64px !important;
}



a.navbar-brand svg.logo {

    color: rgba(var(--bs-dark-rgb), var(--bs-bg-opacity)) !important;
}

a.navbar-brand {

    display: flex;
    align-items: center;
    padding-left: 1rem;
    padding-right: 1rem;
}

.bg-primary .navbar-nav .nav-link.active {

    text-decoration: overline;
    text-decoration-thickness: 3px;
    text-underline-offset: 6px;
    color: #fff !important;
    cursor: pointer;
}

.bg-primary .navbar-nav a.nav-link:hover {

    text-decoration: underline;
    text-decoration-thickness: 3px;
    text-underline-offset: 6px;
    color: #fff !important;

}


.navbar .nav-sep {

    padding-left: 2px !important;
    padding-right: 2px !important;
    color: #fff !important;
}

.navbar .nav-sep:hover {
    text-decoration: none ! important;
    color: #fff !important;

}
